<template>
	<view class="wrapper padding">
		<view class="search">
			<image src="../../static/search_black.png" class="search_icon"></image>
			<input 
				type="text" 
				placeholder="请输入4S店名称" 
				v-model="paramsInfo.name"
				confirm-type="search"
				@confirm="search"
				:focus="Focus"
				>
				<view class="clear" @click="clear">
					<image src="../../static/clear.png" class="clear"></image>
				</view>
		</view>
		<scroll-view class="list_wrapper" scroll-y="true" lower-threshold="50" @scrolltolower="scrollBottom">
			<view class="list" v-if="!empty">
				<view class="item" v-for="(item,index) in listData" :key="index" @click="selectAdress(item)">
					<image src="../../static/search_gray.png" class="search_icon"></image>
					<view class="txt">{{item.name}}</view>
					<image src="../../static/up_arrow.png" class="clear"></image>
				</view>
				<loadMore :status="status" :content-text="textDesc"></loadMore>
			</view>
			<view class="no_data" v-else>
				暂无数据
			</view>
		</scroll-view>
	</view>
</template>

<script>
	import { formatTime,http2Https } from '../../common/tools.js'
	import loadMore from "../../components/loadMore/loadMore.vue"
	export default {
		data() {
			return {
				textDesc:{contentdown: "加载更多",contentrefresh: "正在加载...",contentnomore: "没有更多数据了"},
				status:"noMore", //可选值：more（loading前）、loading（loading中）、noMore（没有更多了
				paramsInfo:{
					name:'',
					pageNum:1,//当前页
					pageSize:10,//每页多少条,
					queryType:0  //0 是4s店，   1 是地图页进来的
				},
				empty:false,
				listData:[],
				Focus:true,
				fromIndex:'',//第几个选择的
				fromPage:1 ,// 1 是录入信息时，2编辑订单也，3 是地图页时
				cityCode:''
			};
		},
		methods:{
			search(){
				this.listData=[]
				this.paramsInfo.pageNum=1
				this.getList()
			},
			clear(){
				this.paramsInfo.name='';
				this.listData=[]
				this.status="noMore"
				
			},
			selectAdress(item){
				var pages = getCurrentPages();
				var prevPage = pages[pages.length - 2];
				if(this.fromPage==1){
					prevPage.$vm.orderList[this.fromIndex].store4sName=item.name
					prevPage.$vm.orderList[this.fromIndex].store4sId=item.id
				}else if(this.fromPage==2){
					prevPage.$vm.paramInfo.store4sId=item.id
					prevPage.$vm.paramInfo.store4sName=item.name
				}else{ //地图页进来
					prevPage.$vm.selectShopInfo=item
					prevPage.$vm.shopList=[item]
				}
				uni.navigateBack({
					delta:1
				})
			},
			
			getList(){
				if(!this.paramsInfo.name){ uni.showToast({title: '请输入4S店名称',icon:'none'});this.Focus=true;return}
				if(this.fromPage==3){ //地图页进来的 
					this.paramsInfo.city=this.cityCode
					this.paramsInfo.queryType=1 // 地图
				}else{
					this.paramsInfo.queryType=0 //
				}
				this.request("/store/list",this.paramsInfo).then((res)=>{
					if(res.resultCode==0){
						let _data=res.data
						if(_data.list.length>0){ //如果有数据
							this.empty=false
							this.listData=this.listData.concat(_data.list)
							if(_data.list.length<this.paramsInfo.pageSize){ //如果有数据但是 小于 每页数量
								this.status="noMore"
							}else{
								this.paramsInfo.pageNum++
								this.status="more"
							}
						}else{ //如果这次没数据
							this.empty=false
							this.status="noMore"
							if(this.paramsInfo.pageNum==1){ //说明开始就没数据
								this.empty=true
							}
						}
					}else{
						uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常,请稍后重试',icon:'none'})
					}
					uni.stopPullDownRefresh()
				})
			},
			scrollBottom(){ //加载更多
				if(this.status!="noMore"){
					this.getList()
				}
			}
		},
		components:{
			loadMore
		},
		onLoad(params){
			this.fromIndex=params.idx
			this.fromPage=params.fromPage
			this.cityCode=params.cityCode
		}
	}
</script>

<style lang="stylus">
	.wrapper
		.search
			display:flex
			flex-direction row
			align-items center
			height:112upx
			border-bottom 2upx solid #E3E9F0
			input
				width:77%
				font-size 32upx
				margin:0upx 30upx
			.clear
				flex:1
				text-align right
			image
				width:28upx
				height:28upx
		.title
			margin-top:58upx
			font-size:40upx;
			font-family:PingFangSC-Semibold,PingFang SC;
			font-weight:600;
			color:rgba(30,30,30,1);
		.list_wrapper
			height:85vh
			.list
				margin-top:33upx
				.item
					display:flex
					flex-direction row
					align-items center
					height:50upx
					margin-bottom:60upx
					image
						width:28upx
						height:28upx
					.txt
						margin:0upx 30upx
						flex:1
						text-overflow ellipsis
						overflow hidden
						white-space nowrap
</style>
<style lang="stylus">
	.no_data
		text-align:center
		font-size:32upx;
		font-family:PingFangSC-Regular;
		font-weight:400;
		height:30vh;
		line-height:30vh;
		color:rgba(203,210,219,1);	
</style>